.avatar {
    width: 80px;
    height: 80px;
    border-radius: 50%;

}

.element {
    margin: 20px;
    width: 600px;
    height: 200px;
    color: white;
}

.page {
    color: white;
    font-size: 16px;
    margin: 5px;
}

.selectPage {
    color: white;
    font-size: 18px;
    font-weight: bold;
    margin: 5px;
    cursor: pointer;
}
.page:hover{
    cursor: pointer;
}